.sidebar {
  background-color: transparent;
  height: 100vh;
  border-radius: 0px 0px 8px 0px;
  color: #eeeeee;
  z-index: 50;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1.5rem;
  height: 100vh;
  width: 77px;
  transition: all 0.5s ease;
  position: fixed;
}
@media (min-width: 768px) {
  .sidebar {
    background-color: rgba(0, 12, 18, 0.5);
    backdrop-filter: blur(1.7px);
  }
}
.sidebar .head-sidebar {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 2rem;
  position: relative;
  transition: all 0.5s ease;
}
.sidebar .head-sidebar #btnSidebar {
  position: absolute;
  top: 50%;
  right: 78px;
  transform: translateY(-50%);
  font-size: 3rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.5s ease;
  margin: 0;
}
.sidebar .head-sidebar i {
  color: #000C12;
}
@media (min-width: 768px) {
  .sidebar .head-sidebar i {
    color: #eeeeee;
  }
}
.sidebar .head-sidebar p {
  font-weight: bold;
}
.sidebar .head-sidebar .icon,
.sidebar .head-sidebar p {
  opacity: 0;
  transition: all 0.5s ease;
}
.sidebar i {
  color: #000C12;
  height: 60px;
  min-width: 5rem;
  font-size: 2.8rem;
  text-align: center;
  line-height: 6rem;
  transition: all 0.5s ease;
}
.sidebar.open {
  background-color: rgba(0, 12, 18, 0.5);
  backdrop-filter: blur(1.7px);
  width: 322px;
  top: -1px;
  z-index: 110;
}
.sidebar.open .head-sidebar {
  margin-right: 53px;
}
.sidebar.open .head-sidebar i {
  color: #eeeeee;
}
.sidebar.open .head-sidebar #btnSidebar {
  right: 0;
  margin-right: -30px;
  text-align: right;
}
.sidebar.open .head-sidebar .icon,
.sidebar.open .head-sidebar p {
  opacity: 1;
}
.sidebar.open .nav-list {
  width: auto;
  height: auto;
  opacity: 1;
  display: block;
}
.sidebar.open .nav-list button {
  padding-right: 1rem;
  width: 85%;
}
.sidebar.open .nav-list button:hover {
  transform: scale(1.05);
}
.sidebar.open .nav-list .nombre-link {
  width: auto;
  height: auto;
  opacity: 1;
  display: block;
}
.sidebar.open .nav-list .perfil-sidebar .perfil-contenedor {
  padding: 2rem;
  margin: 0 auto 2rem;
  gap: 3rem;
  border-radius: 0;
}
.sidebar.open .nav-list .perfil-sidebar .perfil-contenedor:hover {
  transform: scale(1.05);
}
.sidebar.open .nav-list .perfil-sidebar .perfil-contenedor img {
  width: 6rem;
  height: 6rem;
  transform: none;
}
.sidebar.open .nav-list .perfil-sidebar .perfil-contenedor .perfil-detalles {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  opacity: 1;
  width: auto;
  height: auto;
}

.nav-list {
  margin: 0;
  padding: 0;
  opacity: 0;
  width: 0;
  height: 0;
  display: none;
}
@media (min-width: 768px) {
  .nav-list {
    opacity: 1;
    width: auto;
    height: 100vh;
    display: block;
  }
}
.nav-list li {
  display: flex;
  align-items: center;
  list-style-type: none;
  margin: 0 auto 1rem;
}
.nav-list li .actual i,
.nav-list li .actual .nombre-link {
  font-weight: 600;
  color: #329f00;
}
.nav-list li button {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 8px;
  border: none;
  display: flex;
  margin: 0 auto;
  align-items: center;
  color: #eeeeee;
  text-align: left;
  cursor: pointer;
  transition: all 0.5s ease;
}
.nav-list li button:hover i {
  transform: scale(1.25);
}
.nav-list li button:hover i,
.nav-list li button:hover .nombre-link {
  color: #329f00;
  transition: all 0.5s ease;
}
.nav-list li i {
  color: #eeeeee;
}
.nav-list li .nombre-link {
  width: 0;
  height: 0;
  opacity: 0;
  font-size: 1.6rem;
  margin: 0;
  display: none;
}
.nav-list li .nombre-link::after {
  content: " »";
  font-size: 2rem;
}

.perfil-sidebar {
  transition: all 0.5s ease;
  overflow: hidden;
  cursor: pointer;
}
.perfil-sidebar .perfil-contenedor {
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 8px;
  width: 100%;
  padding: 1rem;
  transition: all 0.5s;
}
.perfil-sidebar .perfil-contenedor:hover img {
  filter: brightness(80%);
  transform: scale(1.11);
}
.perfil-sidebar .perfil-contenedor:hover .perfil-nombre,
.perfil-sidebar .perfil-contenedor:hover .perfil-nivel {
  filter: brightness(82%);
}
.perfil-sidebar .perfil-contenedor.perfilActive img {
  border: 2px #329f00 solid;
}
.perfil-sidebar .perfil-contenedor img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-position: center center;
  background-repeat: no-repeat;
  object-fit: cover;
}
.perfil-sidebar .perfil-contenedor img,
.perfil-sidebar .perfil-contenedor .perfil-nivel,
.perfil-sidebar .perfil-contenedor .perfil-nombre {
  transition: all 0.5s;
}
.perfil-sidebar .perfil-contenedor .perfil-detalles {
  width: 0;
  height: 0;
  opacity: 0;
}
.perfil-sidebar .perfil-contenedor .perfil-detalles p {
  color: #eeeeee;
  font-size: 1.6rem;
  margin: 0;
}
.perfil-sidebar .perfil-contenedor .perfil-detalles .perfil-nivel {
  color: #32A78A;
}

