@use 'base/variables' as v;
@use 'base/mixins' as m;

header {
    background: rgba(0, 0, 0, .82);
    padding: 1.6rem 0;
    position: sticky;
    backdrop-filter: blur(5px);
    left: 0;
    top: 0;
    right: 0;
    z-index: 100;
}

.logo {
    display: flex;
    flex-direction: row;
    align-items: center;

    .logo__txt {
        color: v.$blanco;

        @include m.telefono {
            width: auto;
        }

        span {
            font-family: v.$fuente_principal;
            color: v.$verde;
        }
    }
}


.barra {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 0;
    align-items: center;
}


.log-header {
    display: flex;
    align-items: center;
    gap: 2rem;

    a {
        color: v.$verde;
        transition: color .3s;

        &:hover {
            color: v.$azul;
        }
    }

    i {
        font-size: 5rem;
        color: v.$verde;
    }

    img {
        cursor: pointer;
        width: 4rem;
        height: 4rem;
        margin: auto auto;
        border-radius: 50%;
        background-position: center center;
        background-repeat: no-repeat;
        object-fit: cover;
        border: 2px darken(v.$gris, 25) solid;
    }
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5rem;

    a {

        color: v.$blanco;
    }
}