@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario-account {
    display: flex;
    flex-direction: column;
    margin: 3rem auto;
    width: 85%;

    @include m.tablet {
        width: 80%;
    }

    h4 {
        margin: 3rem auto;

        &:first-of-type {
            margin-top: 0;
        }
    }

    .close-account {
        text-align: center;
        margin-bottom: 2rem;
    }
}

.campo-account {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 2rem;

    @include m.tablet {
        flex-direction: row;
        margin-bottom: 2rem;
        flex-wrap: wrap;
        label {
            align-self: center;
            flex: 0 0 140px;
        }
        input, textarea{
            flex: 0 0 100%;

        }
        textarea{
            height: 10rem;
        }
    }
    
    .text-error{
        text-align: left;
        margin: 0;
        color: v.$rojo;
        font-size: 1.6rem;
    }

    &:last-of-type {
        margin-bottom: 3rem;
    }

}



/* *** */



.campo_nombre-error,
.campo_apellido-error,
.campo_dni-error,
.campo_file-error,
.campo_url-error,
.campo_input-error {

    input {
        border: 2px solid v.$rojo;

        &:focus {
            border: 2px solid v.$rojo;
        }

        &:hover {
            border: 2px solid v.$rojo;
        }
    }
}


.campo_biografia-error {
    textarea {
        border: 2px solid v.$rojo;

        &:focus {
            border: 2px solid v.$rojo;
        }

        &:hover {
            border: 2px solid v.$rojo;
        }
    }
}

.campo_error {
    label {
        color: v.$rojo;
    }
}

.campo_nombre-correcto,
.campo_apellido-correcto,
.campo_dni-correcto {
    input {
        border: 2px solid v.$azul;

        &:focus {
            border: 2px lighten(v.$azul, 10) solid;
        }

        &:hover {
            border: 2px lighten(v.$azul, 10) solid;
        }
    }
}

.campo_biografia-correcto {
    textarea {
        border: 2px solid v.$azul;

        &:focus {
            border: 2px lighten(v.$azul, 10) solid;
        }

        &:hover {
            border: 2px lighten(v.$azul, 10) solid;
        }
    }
}

.campo_correcto {
    label {
        color: v.$azul;
    }
}