@use 'base/variables' as v;
@use 'base/mixins' as m;

.cards {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 5rem auto;
    gap: 4rem;
    align-items: center;

    @include m.tablet {
        flex-direction: row;
        max-width: 55rem;
    }
}

.card {
    background-color: v.$gris;
    border-radius: 8px;
    border: 1px darken(v.$gris, 25) solid;
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
    max-width: 25rem;
    width: 100%;
    margin: 0 auto;
    white-space: nowrap;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    svg {
        width: 4rem;
    }
}

.contenido_card {
    display: flex;
    flex-direction: row;
    padding: 2rem;
    justify-content: center;
    align-items: center;

    &:hover {
        .arrow {
            transform: translateX(0%);
        }

        .card_text {
            opacity: 0;
        }
    }
}

.arrow {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0px;
    width: 100%;
    opacity: 1;
    height: 100%;
    transform: translateX(-100%);
    transition: transform .4s ease;
    background-color: v.$azul;
    border-radius: 8px;

    i {
        font-size: 3rem;
        color: v.$gris;
    }
}

.card_text {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity .2s ease;
    color: v.$negro;
}